___CSS_LOADER_EXPORT___.push([module.id, `.revolving__container {
  position: absolute;
  bottom: 30px;
  width: 100vw;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 1;
  transition: opacity 0.6s linear;
}
.revolving__container .text {
  padding: 0;
  color: #AAA;
  font-size: 0.6em;
  padding-right: 120px;
  letter-spacing: 5px;
  font-weight: 300;
}
@media only screen and (max-width: 670px) {
  .revolving__container .text {
    font-size: 0.5em;
    padding-right: 110px;
  }
}
.revolving__container p {
  display: inline-block;
  vertical-align: top;
  margin: 0;
}
.revolving__container .word {
  position: absolute;
  opacity: 0;
  color: #333;
  font-weight: 400;
}
.revolving__container .letter {
  display: inline-block;
  position: relative;
  transform: translateZ(25px);
  transform-origin: 50% 50% 25px;
}
.revolving__container .letter.out {
  transform: rotateX(90deg);
  transition: transform 0.32s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}
.revolving__container .letter.behind {
  transform: rotateX(-90deg);
}
.revolving__container .letter.in {
  transform: rotateX(0deg);
  transition: transform 0.38s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
@media only screen and (max-width: 670px) {
  .revolving__container {
    bottom: calc(80vh);
  }
}
