// Module
___CSS_LOADER_EXPORT___.push([module.id, `.linkElement {
  display: inline-flex;
  padding: 0 10px 0 0;
}
.linkElement .logo {
  width: 1em;
  height: 1em;
  filter: grayscale(1);
}
@media only screen and (max-width: 670px) {
  .linkElement .logo {
    width: 1.25em;
    height: 1.25em;
  }
}
.linkElement .container {
  display: flex;
  justify-content: right;
  align-items: bottom;
}
.linkElement .subContainer {
  display: none;
  position: fixed;
  margin-top: 1.2em;
  padding-top: 0.8em;
  color: #000;
  z-index: 1;
  text-align: right;
  font-size: 17px;
  border: 1px solid transparent;
}
@media only screen and (max-width: 670px) {
  .linkElement .subContainer {
    padding-top: 1em;
  }
}
.linkElement .item {
  display: inline-block;
}
.linkElement .item a {
  color: black;
  text-decoration: none;
}
@media (hover: none) {
  .linkElement .item a {
    background-color: rgba(255, 255, 255, 0.75);
  }
}
.linkElement .item:hover {
  color: #FFF;
}
.linkElement .linkName {
  display: block;
  position: fixed;
  margin-top: 1.2em;
  padding-top: 0.8em;
  z-index: 1;
  text-align: right;
  font-size: 17px;
  border: 1px solid transparent;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  margin-top: 0.25em;
  color: rgba(0, 0, 0, 0.15);
  cursor: default;
}
.linkElement .logo:hover + .subContainer,
.linkElement .icon:hover + .subContainer,
.linkElement .logo:hover + .linkName {
  display: block;
}
.linkElement .subContainer:hover,
.linkElement .linkName:hover {
  display: block;
}
.linkElement .name {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  margin-top: 0.25em;
  color: rgba(0, 0, 0, 0.15);
  cursor: default;
}
.linkElement .icon {
  font-size: 24px;
  color: SteelBlue;
  filter: grayscale(1);
}
@media only screen and (max-width: 670px) {
  .linkElement .icon {
    font-size: 31px;
  }
}
